import { Injectable } from '@angular/core';
import { Platform, NavController } from '@ionic/angular';
import { Router, NavigationEnd } from '@angular/router';
import { AuthenticationService } from './authentication.service';

@Injectable({
  providedIn: 'root'
})
export class BackbuttonService {
  exitB: any;
  currentURL: any = "";
  haspressB: boolean = false;
  openModal = false;
  /*Este servicio controla el comportamiento del botn atrs en la aplicacin */
  constructor(private platform: Platform,
    private navCtrl: NavController,
    private router: Router,
    private authS: AuthenticationService) {
    this.exitB = this.platform.backButton;
    this.exitB.subscribe(() => {
      window.document.querySelector('ion-app').classList.remove('cameraView');
      if (!this.openModal) {
        if (this.currentURL == "/" || this.currentURL == "" || this.currentURL == "/home" || this.currentURL == "/profesor" || this.currentURL == "/profesor/home" || this.currentURL == "/usuario" || this.currentURL == "/usuario/home")
          /*En caso de estar en la pestaa home si se pulsa atrs se cierra la aplicacin*/
          navigator['app'].exitApp();
        else {
          console.log("toca la magia")
          /*En caso de que no estemos en una pestaa home, al pulsar atrs
          navegaremos a home, pero no podemos realizar aqu la navegacin: por qu?
          Cuando se pulsa atrs, el comportamiento por defecto es ir hacia atrs en el historial
          de navegacin, pero no es una accin inmediata (observable). Por tanto, si intento
          realizar aqu la navegacin ser machacada por la accin por defecto de ir atrs
          en el historial. Qu debo hacer? Observar el navegador y cuando haya terminado 
          de navegar hacia atrs en el historial sobreescribir la ruta con home.
          No es perfecto, pero no he encontrado otro mecanismo
          */
          //this.navCtrl.navigateRoot(['/profesor/home'], { animationDirection: "back" });
          this.haspressB = true;  //indica que se ha pulsado hacia atrs.
        }
      }
    });

    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {  //es lanzado cuando se termina de navegar
        this.currentURL = event.url;
        if (this.haspressB) {  //Si se puls atrs
          if (this.authS.isLogged()) {
            if (this.authS.getRole() == "prof") {  //si soy profe me voy al home de profe
              this.navCtrl.navigateRoot(['/profesor/home'], { animationDirection: "back" });
            } else {  //si soy alumno me voy al home de alumno
              this.navCtrl.navigateRoot(['/usuario/home'], { animationDirection: "back" });
            }
          } else {  //Este no debe ser alcanzado nunca, es por si acaso
            this.navCtrl.navigateRoot(['/'], { animationDirection: "back" });
          }

          this.haspressB = !this.haspressB;  //Ya he gestionado la accin, dejo de indicar que se puls atrs
        }
      }
    });

  }
}
